.watched {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%) scale(1);
}
.gallery {
  padding-top: 250px;
  padding-bottom: 40px;

  transition: all var(--transition);
  @include tablet {
    padding-top: 290px;
    padding-bottom: 60px;
  }
}
.gallery__list {
  width: 100%;
  @include tablet {
    display: flex;
    flex-wrap: wrap;
    gap: 32px;
  }
  @include desktop {
    gap: 16px;
  }
}

.card__item {
  display: block;
  width: 280px;

  @media screen and (max-width: 767px) {
    margin: 0 auto;
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
  @include tablet {
    width: 336px;
  }
  @include desktop {
    width: 393px;
  }
}
.card__link {
  display: block;
  width: 100%;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    transform: scale(0.95);
  }
}
.card__link:hover .card__img {
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3),
    -23px 0 20px -23px rgba(0, 0, 0, 0.6), 23px 0 20px -23px rgba(0, 0, 0, 0.6),
    inset 0 0 40px rgba(0, 0, 0, 0.1);
}
.card__title {
  margin-top: 10px;
  @include text(500, 12px, 16px);
  color: var(--color-title);
  text-transform: uppercase;
  @include desktop {
    @include text(500, 20px, 24px);
    margin-top: 12px;
  }
}
.card__text {
  display: flex;
  @include text(500, 12px, 16px);
  color: var(--accent);
  @include desktop {
    @include text(500, 20px, 24px);
  }
}
.card__rating {
  width: 36px;
  height: 16px;
  border-radius: 5px;
  background-color: var(--accent);
  color: var(--header-color-text);
  @include text(500, 12px, 14px);
  margin-left: 8px;
  @include flexCenter;
  @include desktop {
    width: 40px;
    height: 20px;
  }
}
.card__img {
  max-width: 100%;
  height: 398px;
  border-radius: 5px;
  transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
  @include tablet {
    height: 455px;
  }
  @include desktop {
    height: 574px;
  }
}
